//高德SDK
import amap from '@/common/SDK/amap-wx.js';

var loginResult;

export default {
	onLoad(options) {
		loginResult = this.checkLogin('/pages/tabbar-2/tabbar-2', 0);
		if(!loginResult){return;}
		
		//高德地图
		this.amapPlugin = new amap.AMapWX({
			//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
			key: 'c08443fc8d0bf377d98806a2f30d565a'
		});
		//定位地址
		this.amapPlugin.getRegeo({
			success: data => {
				this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉



			}
		}); 
		console.log(options);
	},
	
	onShow() {
		uni.showLoading({
			title: '加载数据中...',
			mask: false
		});
		//获取城市
		this.getCity();
		//获取约拍信息
		this.getAppointmentAndUserByCity(this.city);
		//this.getAppointmentUser();
		//获取天气信息
		this.getWeatherInfo(this.city);
	},
	
	onPullDownRefresh() {
		this.getAppointmentAndUserByCity(this.city);
		setTimeout(function(){
			uni.stopPullDownRefresh();
		}, 1000);
		
	},
	onReachBottom() {
		
	},
	
	data() {
		return {
			br:'\n',
			refreshing: false,
			lists: [],
			fetchPageNum: 1,
			
			showHeader:true,
			afterHeaderOpacity: 1,//不透明度
			headerPosition: '',
			
			
			statusTop:null,
			nVueTitle:null,
			city: '北京',
			currentSwiper: 0,
			
			appointmentData:{},
			weather:{},
			empty:true,
			
			
		}
	},
	
	methods: {
		goPage:function(url){
			uni.navigateTo({
				url: url,
				success: res => {
					console.log('navigateTo-->url:');
					console.log(url);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//获取约拍信息
		getAppointmentUser:function(){
			uni.request({
				url: this.createApiUrl('appointment/get/apt-user'),
				method: 'GET',
				data: {},
				success: res => {
					console.log(res);
					this.appointmentData = res.data.data;
					
				},
				fail: () => {
					
				},
				complete: () => {
					uni.hideLoading();
				}
			});
		},
		
		//通过城市获取约拍信息
		getAppointmentAndUserByCity:function(city){
			uni.request({
				url: this.createApiUrl('appointment/get/apt-city'),
				method: 'GET',
				data: {
					city:city
				},
				success: res => {
					console.log(res);
					this.empty = true;
					if(res.data.data.length != 0){
						this.empty = false;
					}
					this.appointmentData = res.data.data;
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading();
				}
			});
		},
		
		//获取城市
		getCity:function(){
			let city = uni.getStorageSync('apt-city');
			console.log(city);
			this.city = city;
		},
		
		//获取天气信息
		getWeatherInfo:function(city){
			uni.request({
				url: 'http://jisutqybmf.market.alicloudapi.com/weather/query',
				method: 'GET',
				header:{
					Authorization: "APPCODE 22d4a321bdaf4eefbe6709bd7d4e2ed1"
				},
				data: {
					city:city
				},
				success: res => {
					console.log(res);
					this.weather = res.data.result;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//获取天气图片：传入天气图片文件名
		getWeatherImg:function(imgName){
			return '/static/icon/weathercn/' + imgName + '.png';
		}
		
	}
}
